import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useUser from '../../../hooks/useUser';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Edit as EditIcon, Trash } from 'react-feather';
import userService from '../../../services/userService';
import { useTranslation } from 'react-i18next';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function UserList() {
  const { t } = useTranslation();
  const { users, deleteUser } = useUser();

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteUser, setDeleteUser] = React.useState({});

  const handleClickOpen = (user) => {
    setOpen(true);
    setDeleteUser(user);
  };

  const handleDeleteUser = async () => {
    await deleteUser(needDeleteUser.id);
    setOpen(false);
    setDeleteUser({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name'
    },
    {
      title: t('Email'),
      field: 'email'
    },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/users/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!users) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('User List')}>
      <Container maxWidth={false}>
        <Header />
        {users && (
          <Box mt={3}>
            <MaterialTable title={t('User List')} columns={columns} data={users} />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteUser}
        checkText={needDeleteUser.name}
      />
    </Page>
  );
}
